import { createStore } from 'vuex'

export default createStore({
  state: {
    pageSettings: {
      collapse: false
    }
  },
  getters: {
    isNavBarCollapsed(state){
      return state.pageSettings.collapse;
    }
  },
  mutations: {
    changeCollapse(state, newVal){
      state.pageSettings.collapse = newVal;
    }
  },
  actions: {

  },
  modules: {

  }
})
