import { createRouter, createWebHashHistory } from 'vue-router'
export const routes = [
  {
    path: '/',
    name: 'root',
    redirect: "dashboard",
    rootObj: true
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard.vue'),
  },
  {
    path: '/drivers',
    name: 'Drivers',
    redirect: "/drivers/view",
    children: [
        {
          path: 'view',
          name: 'View',
          component: () => import(/* webpackChunkName: "about" */ '../views/ViewDrivers.vue'),
        },
      {
        path: 'addDriver',
        name: 'Add Driver',
        component: () => import(/* webpackChunkName: "about" */ '../views/AddDrivers.vue'),
      }
    ]
  },
  {
    path: '/rides',
    name: 'Rides',
    redirect: "/rides/ongoing",
    children: [
      {
        path: 'ongoing',
        name: 'Ongoing',
        component: () => import(/* webpackChunkName: "about" */ '../views/OngoingRides.vue'),
      },
      {
        path: 'completed',
        name: 'Completed',
        component: () => import(/* webpackChunkName: "about" */ '../views/CompletedRides.vue'),
      }
    ]
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
